$(document).on( "pageinit", "#page-sff", function(event) {

  App.StartLoading();

  $(".numura-warning").loadWarning();  

  $("#shortname").html(filterXSS(sessionStorage.getItem("FundShortName")));
  $("[data-role='moneytype']").loadMoneyType(JSON.parse(sessionStorage.getItem("MoneyDividendTypes")));

	$.ajax({
		url: Constants.getFundFileUrl(),
      	type:"GET"
  	}).done(function(xml){
      var $fundInfo = $(xml).find("[FundConCode='" + sessionStorage.getItem("coalitionFundCode") + "']");
      $("[data-role='download']").each(function(index){
        var dataType = $(this).data("type");
        var item = $fundInfo.find("[EnName='" + dataType + "']");
        var dataFile = item.attr("ChName");
        $(this).data("file",dataFile);
      });
      App.StopLoading();
	});  

});

$(document).on( "pageshow", "#page-sff", function(event) {

  $("[data-role='page']").css({
    "padding-top":$("[data-role='header']").outerHeight(true) + "px"
  });

});

$(document).on("vclick","[data-role='download']",function(event){
  var filepath = $(this).data("file");
  if(_.isEmpty(filepath)){
    App.ShowAlert('很抱歉，檔案尚待更新',"",'確定');
  }else{
    App.OpenFile(Constants.filePrefixUrl + filepath);
  }
  return false;
});